@import url("https://fonts.googleapis.com/css2?family=Poppins:ital,wght@0,100;0,300;0,400;0,500;0,600;0,700;0,800;0,900;1,400;1,500;1,700;1,800&display=swap");
ul {
  padding: 0;
  display: flex;
}

main {
  flex: 1 0 auto;
}

#hero {
  height: 787px;
  border-bottom-left-radius: 5em;
  background-image: url("../img/background-desk.png");
  background-color: white;
  background-size: cover;
  background-repeat: no-repeat;
  display: flex;
  justify-content: center;
  flex-direction: column;
  align-items: center;
  padding: 0 10px 0 10px;
}

.ul-tags {
  padding-left: 0;
}
.ul-tags li {
  padding: 3px 14px 3px 14px;
  background-color: #ebebff;
  border-radius: 8px;
  font-size: 12px;
  line-height: 16px;
  color: #6070ff;
  font-weight: 500;
  list-style: none;
  margin-right: 2em;
  margin-bottom: 3em;
}

.ul-details {
  display: flex;
}
.ul-details li {
  list-style: disc;
  margin-right: 25px;
}

#navbar > ul > li {
  margin-right: 2em;
  list-style: none;
}

.portfolio-items {
  width: 80%;
}

.about-section {
  width: 100%;
  border-top-right-radius: 5em;
  background-color: white;
}

.project {
  margin-top: 40px;
  background-color: white;
  padding: 30px;
  border-radius: 15px;
}

.work-details {
  display: flex;
}
.work-details .p-details {
  margin-right: 25px;
}

.modal-dialog,
.modal-content {
  background: rgba(96, 112, 255, 0.5);
  backdrop-filter: blur(5px);
}

.modal-content {
  padding-top: 20px;
  padding-right: 20px;
}

.modal-backdrop {
  display: none;
}

.btn-close-white {
  opacity: 1;
}

.menu-mobile-li {
  font-size: 30px;
  color: white;
  list-style: none;
}
.menu-mobile-li a {
  color: white;
  font-weight: 500;
}

.fa-bars {
  color: #6070ff;
}

.hamburger {
  border: none;
}

@media (max-width: 557px) {
  #portfolio {
    display: block;
  }
}
h1 {
  color: #091e42;
}

h2 {
  color: #091e42;
}

h3 {
  color: #091e42;
}

h4 {
  color: #091e42;
}

h5 {
  color: #091e42;
}

h6 {
  color: #091e42;
}

h1 {
  font-size: 48px;
  line-height: 60px;
}

h2 {
  font-size: 40px;
  line-height: 52px;
}

a {
  font-weight: 500;
}

body {
  background-color: #f7f7f9;
}

header {
  background-color: white;
}

main {
  background-color: #f7f7f9;
}

footer {
  color: white;
  background-color: white;
}

.fab {
  font-size: 1.2rem;
}

.btn-primary {
  color: #396df2;
  background-color: white;
  border-color: #396df2;
  border-radius: 8px;
}

.footerholder {
  background-color: #6070ff;
  border-top-left-radius: 5em;
}

.logo {
  text-decoration: none;
  font-size: 20px;
}

#contact {
  border-top-left-radius: 8em;
  background-image: url("../img/background-desk.png");
  background-size: cover;
  background-repeat: no-repeat;
  border-bottom-left-radius: 5rem;
  display: flex;
  justify-content: center;
  flex-direction: column;
  align-items: center;
}

a,
p,
h1,
h2,
h3,
h4,
ul,
li,
form,
button {
  font-family: "Poppins", sans-serif;
}

.tools {
  background-color: #f7f7f9;
  flex: 1;
}

.hamburger {
  background-color: white;
}

.accordion-button:not(.collapsed) {
  background-color: white;
}

.accordion-button:focus {
  border: none;
  box-shadow: none;
}